"use client";

import { useState, useRef, useEffect } from "react";
import { Marquee, useMarquee, type MarqueeRef } from "@townhall-gg/marquee-react";

// Icons
function PlayIcon() {
  return (
    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 32 32" fill="none" className="w-8 h-8">
      <path stroke="currentColor" strokeWidth="2.5" d="M8 6v20l18-10L8 6Z" />
    </svg>
  );
}

function PauseIcon() {
  return (
    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 32 32" fill="none" className="w-8 h-8">
      <path
        stroke="currentColor"
        strokeWidth="2.5"
        d="M6.5 5.5H12v21H6.5V5.5ZM20 5.5h5.5v21H20V5.5Z"
      />
    </svg>
  );
}

function LeftArrow() {
  return (
    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 32 32" fill="none" className="w-8 h-8">
      <path
        stroke="currentColor"
        strokeLinecap="square"
        strokeWidth="2.5"
        d="m13 8-8 8 8 8m-6-8h20"
      />
    </svg>
  );
}

function RightArrow() {
  return (
    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 32 32" fill="none" className="w-8 h-8">
      <path
        stroke="currentColor"
        strokeLinecap="square"
        strokeWidth="2.5"
        d="m19 8 8 8-8 8m6-8H5"
      />
    </svg>
  );
}

function StarIcon({ className }: { className?: string }) {
  return (
    <svg viewBox="0 0 86 87" fill="none" xmlns="http://www.w3.org/2000/svg" className={className}>
      <path
        d="M56.9097 27.5281L43.7907 0.0351563L30.6717 27.5281L0.470276 31.5093L22.5637 52.482L17.0172 82.4354L43.7907 67.9043L70.5641 82.4354L65.0176 52.482L87.111 31.5093L56.9097 27.5281Z"
        fill="currentColor"
      />
    </svg>
  );
}

// Control button
function ControlButton({
  onClick,
  active,
  children,
  className = "",
}: {
  onClick: () => void;
  active?: boolean;
  children: React.ReactNode;
  className?: string;
}) {
  return (
    <button
      onClick={onClick}
      className={`btn w-16 h-16 rounded-md text-lg ${active ? "btn-primary" : "btn-secondary"} ${className}`}
    >
      {children}
    </button>
  );
}

